<template>
  <div v-if="tarefaId" class="card shadow mt-3">
    <header class="card-header">
      <strong class="align-self-center"> Chat da taféra</strong>
    </header>
    <div class="card-body">
      <p class="text-center">
        mensagens mais recentes <i class="fas fa-arrow-up"></i>
      </p>
      <div class="row">
        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 chat">
          <div class="row">
            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12">
              <div v-for="(column, index) in chatMensage" :key="index">
                <template v-if="column.mensagemEnvida == true">
                  <!-- chat inviado  -->
                  <div class="d-flex justify-content-end">
                    <div class="container containerChat col-10">
                      <div class="d-flex justify-content-end mb-1">
                        <!-- <i class="fas fa-laugh-wink"></i> -->
                        <span class="time-right mr-3"> {{ column.user }}</span>
                        <i class="fas fa-arrow-up"></i>
                      </div>
                      <div class="d-flex justify-content-end">
                        <p>{{ column.mensagem }}</p>
                      </div>

                      <span class="time-right">{{ column.data }}</span>
                    </div>
                  </div>

                  <!-- fim chat inviado  -->
                </template>
                <template v-if="column.mensagemEnvida == false">
                  <!-- chat recebido -->
                  <div class="container containerChat darker col-10">
                    <div class="d-flex justify-content-start mb-1">
                      <!-- <i class="fas fa-laugh-wink "></i> -->
                      <i class="fas fa-arrow-down mr-3"></i>
                      <span class="time-left">{{ column.user }}</span>
                    </div>
                    <p>{{ column.mensagem }}</p>

                    <span class="time-left">{{ column.data }}</span>
                  </div>
                  <!-- fim chat recebido -->
                </template>
              </div>
            </div>
          </div>
        </div>
        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12">
          <div class="row">
            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12">
              <div class="input-group mb-3">
                <textarea
                  v-model="mensagemChat"
                  rows="4"
                  class="form-control"
                  placeholder="Digite uma mensagem"
                />
                <div class="input-group-append">
                  <input
                    type="button"
                    @click="enviaMensagemChat"
                    class="input-group-text"
                    value="Enviar"
                  />
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
/**
 * Coisas a fazer no componete
 *
 * 1° - url dinamica para funcionar em qualquer lugar ( dentro de tarefa e conjunto de tarefa, pois o no do banco é diferente)
 * 2° - fazer a mensagem scrolar quando enviar e deichar o chat com a mensagem mais recente a baixo como no wthas e outros mensageiros
 *
 */

import { ref /*, onMounted */ } from "vue";
import servico from "../../requisicoes/servicos/usuarios/index";
import firebase from "firebase";
import { useRoute } from "vue-router";
export default {
  setup() {
    const {
      params: { tarefaId, clienteId },
    } = useRoute();
    let url = ""; //tarefa/" + ViewModel.value.idBanco; <-- alterar para pegar o caminho certo ou receper esse parangole
    if (clienteId) {
      url = `conjunto/${clienteId}/tarefas/${tarefaId}/chat`;
    } else {
      url = `tarefa/${tarefaId}/chat`;
    }
    console.log("tarefaId", tarefaId);
    console.log("clienteId", clienteId);
    let ViewModel = ref({
      // id: ref(getId),
      idBanco: ref(),
      chat: ref([]),
    });
    let mensagemChat = ref();
    let chatMensage = ref();
    let contador = 0;
    const obterChat = () => {
      //conjunto/clienteID/tarefas/tarefaId/chat

      if (url != "") {
        let chats = [];
        servico
          .get(url)
          .then((snapshot) => {
            // console.log("snapshot", snapshot.val());
            if (snapshot.exists()) {
              console.log(snapshot.val());
              let data = snapshot.val();

              // console.table(data);
              let userName = "";
              let resp = firebase.auth();
              if (resp) {
                userName = resp.currentUser.email; // tirar isso daqui e colocar no requiciçoes ou savar o usuario em algn estado da vuex
              }

              for (var [key, value] of Object.entries(data)) {
                // alterar para pegar direto o no do chat
                console.log(value, key);
                let obj;
                if (userName == value.user) {
                  obj = {
                    mensagem: value.mensagem,
                    user: value.user,
                    mensagemEnvida: true,
                    data: value.data,
                  };
                } else {
                  obj = {
                    mensagem: value.mensagem,
                    user: value.user,
                    mensagemEnvida: false,
                    data: value.data,
                  };
                }

                chats.push(obj);
              }
              chats.reverse();
              chatMensage.value = chats;
              console.log("chats", chats);
            } else {
              contador++;
              console.log("contador", contador);
              if (contador <= 2) {
                setTimeout(function () {
                  obterChat();
                }, 1000);
              }
              console.log("No data available");
            }
          })
          .catch((error) => {
            console.error(error);
          });
      }
    };
    obterChat();
    const enviaMensagemChat = () => {
      if (mensagemChat.value) {
        let userName = "";
        let resp = firebase.auth();
        if (resp) {
          userName = resp.currentUser.email;
        }
        console.log("mensagemChat", mensagemChat.value);
        console.log("userName", userName);

        var date = new Date();
        var dia = date.getDate();
        var mes = date.getMonth();
        let ano = date.getFullYear();
        var hora = date.getHours();
        var min = date.getMinutes();

        let data = {
          mensagem: mensagemChat.value,
          user: userName,
          data: dia + "/" + mes + "/" + ano + " :" + hora + ":" + min,
        };
        // let url = "tarefa/" + ViewModel.value.idBanco + "/chat";
        servico
          .add(url, data)
          .then(() => {
            console.log("atualizar o chat");
            obterChat();
            mensagemChat.value = "";
          })
          .catch((error) => {
            console.error(error);
          });
      }
    };
    return {
      ViewModel,
      mensagemChat,
      chatMensage,
      enviaMensagemChat,
    };
  },
};
</script>

<style>
.chat {
  max-height: 400px;
  overflow-y: scroll;
}
.containerChat {
  border: 2px solid #dedede;
  background-color: #f1f1f1;
  border-radius: 5px;
  padding: 10px;
  margin: 10px 0;
}
/* Darker chat container */

.darker {
  border-color: #ccc;
  background-color: #ddd;
}
/* Clear floats */

.container::after {
  content: "";
  clear: both;
  display: table;
}
/* Style images */

.container img {
  float: left;
  max-width: 60px;
  width: 100%;
  margin-right: 20px;
  border-radius: 50%;
}
/* Style the right image */

.container img.right {
  float: right;
  margin-left: 20px;
  margin-right: 0;
}
/* Style time text */

.time-right {
  float: right;
  color: #aaa;
}
/* Style time text */

.time-left {
  float: left;
  color: #999;
}
</style>
