<template>
  <form @submit="validarForm">
    <!-- ou @submit.prevent="salvaForm" -->
    <div class="row mt-5">
      <div class="col-sm-12">
        <div class="card shadow">
          <header class="card-header">
            <strong class="align-self-center">
              Crie uma nova campanha</strong
            >
          </header>
          <div class="card-body">
            <div class="row">
              <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 mb-3">
                <div class="row">
                  <div class="col-sm-6 col-md-6 col-lg-6 col-xl-6">
                    <label v-if="tarefaId">
                      Data de criação {{ ViewModel.dataCriacao }}</label
                    >
                  </div>
                  <div class="col-sm-6 col-md-6 col-lg-6 col-xl-6">
                    <label v-if="tarefaId">
                      Data ultima alteração
                      {{ ViewModel.dataUltimaAlteracao }}</label
                    >
                  </div>
                </div>
                <label> Campos com * são de preenchimento obrigatório </label>

                <div class="row">
                  <div class="col-sm-6 col-md-6 col-lg-6 col-xl-6">
                    <label>* Nome da campanha</label>
                    <input
                      required
                      class="form-control"
                      type="text"
                      placeholder="Digite o nome"
                      v-model="ViewModel.nomeCampanha"
                      :disabled="inputReadonly"
                    />
                  </div>
                  <div class="col-sm-6 col-md-6 col-lg-6 col-xl-6">
                    <label>* Mês:</label>
                    <Multiselect
                      v-model="ViewModel.mes"
                      placeholder="Selecione o mês"
                      :options="optionsMes"
                      :disabled="inputReadonly"
                      :required="true"
                      :multiple="true"
                    />
                  </div>

                  <div class="col-sm-6 col-md-6 col-lg-6 col-xl-6">
                    <label>* Verba para a campanha </label>
                    <input
                      required
                      class="form-control"
                      type="text"
                      placeholder="Digite o valor total mensal "
                      v-model="ViewModel.verba"
                      :disabled="inputReadonly"
                    />
                  </div>
                  <div class="col-sm-6 col-md-6 col-lg-6 col-xl-6">
                    <label>* Tempo de venculação (em dias)</label>
                    <input
                      required
                      class="form-control"
                      type="text"
                      placeholder="Digite a quantidade de dias"
                      v-model="ViewModel.tempoVeinculando"
                      :disabled="inputReadonly"
                    />
                  </div>
                  <div class="col-sm-6 col-md-6 col-lg-6 col-xl-6">
                    <label>* Tipo de anuncio</label>
                    <Multiselect
                      v-model="ViewModel.tipoDeRede"
                      placeholder="Selecione o tipo de anuncio"
                      :options="optionsTipo"
                      :disabled="inputReadonly"
                      :required="true"
                      :multiple="true"
                    />
                  </div>
                  <div class="col-sm-6 col-md-6 col-lg-6 col-xl-6">
                    <label>* Pagamento:</label>
                    <Multiselect
                      v-model="ViewModel.formaPagamento"
                      placeholder="Selecione a forma de pagamento"
                      :options="optionsPagemento"
                      :disabled="inputReadonly"
                      :required="true"
                    />
                  </div>
                  <!-- Google -->
                  <template v-if="ViewModel.tipoDeRede == 0">
                    <div class="col-sm-6 col-md-6 col-lg-6 col-xl-6">
                      <label>* Titulo 1 - 30 caracteres</label>
                      <input
                        class="form-control"
                        type="text"
                        placeholder="Digite o titulo 1"
                        v-model="ViewModel.googleTitulo1"
                        :disabled="inputReadonly"
                        maxlength="30"
                        required
                      />
                      <label>* Titulo 2 - 30 caracteres</label>
                      <input
                        class="form-control"
                        type="text"
                        placeholder="Digite o titulo 2 "
                        v-model="ViewModel.googleTitulo2"
                        :disabled="inputReadonly"
                        maxlength="30"
                        required
                      />
                      <label>* Titulo 3 - 30 caracteres</label>
                      <input
                        class="form-control"
                        type="text"
                        placeholder="Digite o titulo 3"
                        v-model="ViewModel.googleTitulo3"
                        :disabled="inputReadonly"
                        maxlength="30"
                        required
                      />
                      <label>* Descição 1 - 90 caracteres</label>
                      <input
                        class="form-control"
                        type="text"
                        placeholder="Digite a descrição 1"
                        v-model="ViewModel.googleDescricao1"
                        :disabled="inputReadonly"
                        maxlength="30"
                        required
                      />
                      <label>* Descição 2 - 90 caracteres</label>
                      <input
                        class="form-control"
                        type="text"
                        placeholder="Digite a descrição 2"
                        v-model="ViewModel.googleDescricao2"
                        :disabled="inputReadonly"
                        maxlength="30"
                        required
                      />
                    </div>
                    <div class="col-sm-6 col-md-6 col-lg-6 col-xl-6">
                      <!-- <label>Link das imgs caso tenha em todos os tamanhos </label>
                      <input
                        class="form-control"
                        type="text"
                        placeholder="Digite o valor total mensal "
                        v-model="ViewModel.verba"
                        :disabled="inputReadonly"
                        required
                      /> -->
                      <label>* Objetivo </label>
                      <input
                        class="form-control"
                        type="text"
                        placeholder="Digite o valor total mensal "
                        v-model="ViewModel.googleObjetivo"
                        :disabled="inputReadonly"
                        required
                      />
                      <label>* Link de redirecionamento </label>
                      <input
                        class="form-control"
                        type="text"
                        placeholder="Digite o valor total mensal "
                        v-model="ViewModel.googleLinkRedirecionamento"
                        :disabled="inputReadonly"
                        required
                      />
                      <label>* Palavras chaves </label>
                      <textarea
                        class="form-control"
                        placeholder="Digite as palavras chaves "
                        v-model="ViewModel.palavraChave"
                        :disabled="inputReadonly"
                        required
                        rows="4"
                      ></textarea>
                    </div>
                  </template>
                  <!-- Instagran -->
                  <template v-if="ViewModel.tipoDeRede == 1">
                    <!-- Insta e face -->
                    <div
                      v-if="ViewModel.tipoDeRede == 1"
                      class="col-sm-6 col-md-6 col-lg-6 col-xl-6"
                    >
                      <label>* Tipo:</label>
                      <Multiselect
                        v-model="ViewModel.tipoPublicacaoinsta"
                        placeholder="Selecione o tipo"
                        :options="optionsTipoPostagem"
                        :disabled="inputReadonly"
                        :required="true"
                        :multiple="true"
                      />
                    </div>

                    <!-- Nova -->
                    <template
                      v-if="
                        ViewModel.tipoPublicacaoinsta == 0 &&
                        ViewModel.tipoDeRede == 1
                      "
                    >
                      <div class="col-sm-6 col-md-6 col-lg-6 col-xl-6">
                        <label>* Link da imagem ou vídeo (google drive) </label>
                        <input
                          class="form-control"
                          type="text"
                          placeholder="Digiteo link da img"
                          v-model="ViewModel.linkImg"
                          :disabled="inputReadonly"
                        />
                      </div>
                      <div class="col-sm-6 col-md-6 col-lg-6 col-xl-6">
                        <label>* Link de redirecionamento</label>
                        <input
                          class="form-control"
                          type="text"
                          placeholder="Digite o link de redirecionamento"
                          v-model="ViewModel.linkRedirecionamento"
                          :disabled="inputReadonly"
                        />
                      </div>
                      <div class="col-sm-6 col-md-6 col-lg-6 col-xl-6">
                        <label>Obcservação</label>
                        <input
                          class="form-control"
                          type="text"
                          placeholder="Digite a observação"
                          v-model="ViewModel.obcervacaoInstaface"
                          :disabled="inputReadonly"
                        />
                      </div>
                      <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12">
                        <label>* Legenda </label>
                        <textarea
                          class="form-control"
                          type="text"
                          placeholder="Digite a Legenda"
                          v-model="ViewModel.legendaInstaFace"
                          :disabled="inputReadonly"
                          rows="4"
                        ></textarea>
                      </div>
                    </template>
                    <!-- Existente -->
                    <template v-if="ViewModel.tipoPublicacaoinsta == 1">
                      <div class="col-sm-6 col-md-6 col-lg-6 col-xl-6">
                        <label>* Link da publicação</label>
                        <input
                          class="form-control"
                          type="text"
                          placeholder="Digite o link da publicação"
                          v-model="ViewModel.linkPublicacao"
                          :disabled="inputReadonly"
                        />
                      </div>
                    </template>
                  </template>

                  <!-- Facebook -->
                  <template v-if="ViewModel.tipoDeRede == 2">
                    <div
                      class="
                        col-sm-12 col-md-12 col-lg-12 col-xl-12
                        alert alert-warning
                      "
                      role="alert"
                    >
                      Facebook indisponivel no momento!
                    </div>
                  </template>
                  <!-- Site -->
                  <template v-if="ViewModel.tipoDeRede == 3">
                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12">
                      <label>* Descição</label>
                      <textarea
                        class="form-control"
                        type="text"
                        placeholder="Digite a descrição"
                        v-model="ViewModel.descriçãoSite"
                        :disabled="inputReadonly"
                      ></textarea>
                    </div>
                  </template>
                </div>

                <div class="d-flex mt-3 buttonsActions">
                  <button
                    v-if="mostrarBtnSalvar"
                    class="btn btn-success"
                    type="submit"
                  >
                    Salvar
                  </button>
                  <input
                    type="button"
                    v-if="mostrarBtnSalvar"
                    class="btn btn-warning ml-2 mr-2"
                    @click="limpaForm()"
                    value="Limpar"
                  />

                  <button
                    class="btn btn-secondary"
                    type="reset"
                    @click="voltar"
                  >
                    Voltar
                  </button>
                </div>
              </div>
              <br /><br /><br />
            
            </div>
          </div>
            
        </div>
        <chat v-if="tarefaId" ></chat>
      </div>
    </div>
  </form>
</template>

<script>
import { ref, onMounted } from "vue";
import Multiselect from "@vueform/multiselect";
import { v4 as uuidv4 } from "uuid";
import Swal from "sweetalert2";
import { useRouter, useRoute } from "vue-router";
import { useStore } from "vuex";
import servico from "../../../requisicoes/servicos/usuarios/index";
import Chat from "../../../components/chat/Chat.vue"

export default {
  components: { Multiselect, Chat },
  setup() {
    const {
      params: { tarefaId, visualizar, clienteId },
    } = useRoute();
    const store = useStore();
    const getId = () => {
      return tarefaId ? tarefaId : store.state.emptyGuid;
    };
    // console.log("cliente id ", clienteId);
    // console.log("tarefaId ", tarefaId);

    let mostrarBtnSalvar;
    let inputReadonly;

    let router = useRouter();
    let optionsTipo = [
      { value: 0, label: "Google" },
      { value: 1, label: "Instagran" },
      { value: 2, label: "Facebook" },
      { value: 3, label: "Site" },
    ];
    let optionsPagemento = [
      { value: 0, label: "Cartão" },
      { value: 1, label: "Boleto" },
    ];
    let optionsTipoPostagem = [
      { value: 0, label: "Nova" },
      { value: 1, label: "Existente" },
    ];
    let optionsMes = [
      { value: 0, label: "Janeiro" },
      { value: 1, label: "Fevereiro" },
      { value: 2, label: "Março" },
      { value: 3, label: "Abril" },
      { value: 4, label: "Maio" },
      { value: 5, label: "Junho" },
      { value: 6, label: "Julho" },
      { value: 7, label: "Agosto" },
      { value: 8, label: "Setembro" },
      { value: 9, label: "Outubro" },
      { value: 10, label: "Novembro" },
      { value: 11, label: "Dezembro" },
    ];
    let optionsUsuarios = ref([]);
    if (visualizar) {
      mostrarBtnSalvar = false;
      inputReadonly = true;
    } else {
      mostrarBtnSalvar = true;
      inputReadonly = false;
    }

    let ViewModel = ref({
      id: ref(getId),
      nomeCampanha: ref(null),
      mes: ref(null),
      verba: ref(null),
      tempoVeinculando: ref(null),
      tipoDeRede: ref(null),
      formaPagamento: ref(null),
      // Google
      googleTitulo1: ref(null),
      googleTitulo2: ref(null),
      googleTitulo3: ref(null),
      googleDescricao1: ref(null),
      googleDescricao2: ref(null),
      palavraChave: ref(null),

      googleObjetivo: ref(null),
      googleLinkRedirecionamento: ref(null),

      // Instagran
      tipoPublicacaoinsta: ref(null),
      linkPublicacao: ref(null),
      linkImg: ref(null),
      linkRedirecionamento: ref(null),
      obcervacaoInstaface: ref(null),
      legendaInstaFace: ref(null),
      //Site
      descriçãoSite: ref(null),
    });
    let mensagemChat = ref();
    let chatMensage = ref();

    const limpaForm = () => {
      ViewModel.value.id = ref(null);
      ViewModel.value.nomeCampanha = ref(null);
      ViewModel.value.mes = ref(null);
      ViewModel.value.verba = ref(null);
      ViewModel.value.tempoVeinculando = ref(null);
      ViewModel.value.tipoDeRede = ref(null);
      ViewModel.value.formaPagamento = ref(null);
      ViewModel.value.googleTitulo1 = ref(null);
      ViewModel.value.googleTitulo2 = ref(null);
      ViewModel.value.googleTitulo3 = ref(null);
      ViewModel.value.googleDescricao1 = ref(null);
      ViewModel.value.googleDescricao2 = ref(null);
      ViewModel.value.palavraChave = ref(null);
      ViewModel.value.tipoPublicacaoinsta = ref(null);
      ViewModel.value.linkPublicacao = ref(null);
      ViewModel.value.linkImg = ref(null);
      ViewModel.value.linkRedirecionamento = ref(null);
      ViewModel.value.obcervacaoInstaface = ref(null);
      ViewModel.value.legendaInstaFace = ref(null);
      ViewModel.value.descriçãoSite = ref(null);
      ViewModel.value.googleObjetivo = ref(null);
      ViewModel.value.googleLinkRedirecionamento = ref(null);
    };

    const validarForm = (e) => {
      e.preventDefault();
      // if (ViewModel.value.atribuidaPara) {
      salvaForm();
      // } else {
      //   Swal.fire({
      //     icon: "warning",
      //     title: `Atenção`,
      //     text: `Campos com * são de preenchimento obrigatório`,
      //   });
      // }
    };
    const salvaForm = () => {
      var data = new Date();
      var dia = data.getDate();
      var mes = data.getMonth();
      let ano = data.getFullYear();
      var hora = data.getHours();
      var min = data.getMinutes();

      console.log("tarefaId", tarefaId);
      if (tarefaId) {
          let data = {
          nomeCampanha: ViewModel.value.nomeCampanha,
          mes: ViewModel.value.mes,
          verba: ViewModel.value.verba,
          tempoVeinculando: ViewModel.value.tempoVeinculando,
          tipoDeRede: ViewModel.value.tipoDeRede,
          formaPagamento: ViewModel.value.formaPagamento,
          googleTitulo1: ViewModel.value.googleTitulo1,
          googleTitulo2: ViewModel.value.googleTitulo2,
          googleTitulo3: ViewModel.value.googleTitulo3,
          googleDescricao1: ViewModel.value.googleDescricao1,
          googleDescricao2: ViewModel.value.googleDescricao2,
          palavraChave: ViewModel.value.palavraChave,
          tipoPublicacaoinsta: ViewModel.value.tipoPublicacaoinsta,
          linkPublicacao: ViewModel.value.linkPublicacao,
          linkImg: ViewModel.value.linkImg,
          linkRedirecionamento: ViewModel.value.linkRedirecionamento,
          obcervacaoInstaface: ViewModel.value.obcervacaoInstaface,
          legendaInstaFace: ViewModel.value.legendaInstaFace,
          descriçãoSite: ViewModel.value.descriçãoSite,
          googleObjetivo: ViewModel.value.googleObjetivo,
          googleLinkRedirecionamento:
            ViewModel.value.googleLinkRedirecionamento,
          dataUltimaAlteracao:
            dia + "/" + mes + "/" + ano + ":" + hora + ":" + min,
        };
        let url = `conjunto/${clienteId}/tarefas/${tarefaId}`;
     
        servico
          .edit(url, data)
           .then((resp) => {
            if (resp == "error") {
              Swal.fire({
                icon: "warning",
                title: `Atenção`,
                text: `A tareda "${ViewModel.value.nomeCampanha}" não foi salva`,
              });
            } else {
              Swal.fire({
                icon: "success",
                title: `Concluido`,
                text: `A tareda "${ViewModel.value.nomeCampanha}" foi salva`,
              });
            }
          })
          .catch((error) => {
            console.error(error);
          });
      } else {
        
        ViewModel.value.id = uuidv4();
        let data = {
          id: ViewModel.value.id,
          nomeCampanha: ViewModel.value.nomeCampanha,
          mes: ViewModel.value.mes,
          verba: ViewModel.value.verba,
          tempoVeinculando: ViewModel.value.tempoVeinculando,
          tipoDeRede: ViewModel.value.tipoDeRede,
          formaPagamento: ViewModel.value.formaPagamento,
          googleTitulo1: ViewModel.value.googleTitulo1,
          googleTitulo2: ViewModel.value.googleTitulo2,
          googleTitulo3: ViewModel.value.googleTitulo3,
          googleDescricao1: ViewModel.value.googleDescricao1,
          googleDescricao2: ViewModel.value.googleDescricao2,
          palavraChave: ViewModel.value.palavraChave,
          tipoPublicacaoinsta: ViewModel.value.tipoPublicacaoinsta,
          linkPublicacao: ViewModel.value.linkPublicacao,
          linkImg: ViewModel.value.linkImg,
          linkRedirecionamento: ViewModel.value.linkRedirecionamento,
          obcervacaoInstaface: ViewModel.value.obcervacaoInstaface,
          legendaInstaFace: ViewModel.value.legendaInstaFace,
          descriçãoSite: ViewModel.value.descriçãoSite,
          googleObjetivo: ViewModel.value.googleObjetivo,
          googleLinkRedirecionamento:
            ViewModel.value.googleLinkRedirecionamento,
          dataCriacao: dia + "/" + mes + "/" + ano + ":" + hora + ":" + min,
          dataUltimaAlteracao:
            dia + "/" + mes + "/" + ano + ":" + hora + ":" + min,
        };
        let url = `conjunto/${clienteId}/tarefas`;
     
        servico
          .add(url, data)
           .then((resp) => {
            if (resp == "error") {
              Swal.fire({
                icon: "warning",
                title: `Atenção`,
                text: `A tareda "${ViewModel.value.nomeCampanha}" não foi salva`,
              });
            } else {
              Swal.fire({
                icon: "success",
                title: `Concluido`,
                text: `A tareda "${ViewModel.value.nomeCampanha}" foi salva`,
              });
            }
          })
          .catch((error) => {
            console.error(error);
          });
      }
    };
    // const status200 = () => {
    //   Swal.fire({
    //     icon: "success",
    //     title: `Concluído`,
    //     text: `A tarefa "${ViewModel.value.value}" foi adicionada`,
    //   });
    //   limpaForm();
    // };

    const voltar = () => {
      router.push("/conjunto/tarefas-cliente/" + clienteId);
    };

    onMounted(async () => {
      const ObterTarefa = async (id) => {
        let url = `conjunto/${clienteId}/tarefas/${id}`;
        servico
          .get(url)
          .then((snapshot) => {
            if (snapshot.exists()) {
              // console.log(snapshot.val());
              let data = snapshot.val();
              // console.table(data);
              ViewModel.value.nomeCampanha = data.nomeCampanha;
              ViewModel.value.mes = data.mes;
              ViewModel.value.verba = data.verba;
              ViewModel.value.tempoVeinculando = data.tempoVeinculando;
              ViewModel.value.tipoDeRede = data.tipoDeRede;
              ViewModel.value.formaPagamento = data.formaPagamento;
              // Google
              data.googleTitulo1 ? ViewModel.value.googleTitulo1 = data.googleTitulo1 : ViewModel.value.googleTitulo1 = null;
              data.googleTitulo2 ? ViewModel.value.googleTitulo2 = data.googleTitulo2 : ViewModel.value.googleTitulo2 = null;
              data.googleTitulo3 ? ViewModel.value.googleTitulo3 = data.googleTitulo3 : ViewModel.value.googleTitulo3 = null;
              data.googleDescricao1 ? ViewModel.value.googleDescricao1 = data.googleDescricao1 :  ViewModel.value.googleDescricao1 = null ;
              data.googleDescricao2 ? ViewModel.value.googleDescricao2 = data.googleDescricao2 : ViewModel.value.googleDescricao2 = null;
              data.palavraChave ? ViewModel.value.palavraChave = data.palavraChave : ViewModel.value.palavraChave = null;
              data.googleObjetivo ? ViewModel.value.googleObjetivo = data.googleObjetivo : ViewModel.value.googleObjetivo =  null;
               data.googleLinkRedirecionamento ? ViewModel.value.googleLinkRedirecionamento =
                data.googleLinkRedirecionamento : ViewModel.value.googleLinkRedirecionamento = null;

              // Instagran
              data.tipoPublicacaoinsta ? ViewModel.value.tipoPublicacaoinsta = data.tipoPublicacaoinsta : ViewModel.value.tipoPublicacaoinsta = null;
              data.linkPublicacao ? ViewModel.value.linkPublicacao = data.linkPublicacao : ViewModel.value.linkPublicacao = null;
              data.linkImg ? ViewModel.value.linkImg = data.linkImg : ViewModel.value.linkImg = null;
              data.linkRedirecionamento ? ViewModel.value.linkRedirecionamento = data.linkRedirecionamento : ViewModel.value.linkRedirecionamento = null;
              data.obcervacaoInstaface ? ViewModel.value.obcervacaoInstaface = data.obcervacaoInstaface : ViewModel.value.obcervacaoInstaface = null;
              data.legendaInstaFace ? ViewModel.value.legendaInstaFace = data.legendaInstaFace : ViewModel.value.legendaInstaFace = null;
              //Site
              data.descriçãoSite ? ViewModel.value.descriçãoSite = data.descriçãoSite : ViewModel.value.descriçãoSite = null;

              // for (var [key, value] of Object.entries(data)) { // não é mais necesario pq eu so trafego o as informaçoes do tarefa
              //   if (id == value.id) {
              //     ViewModel.value.id = value.id;
              //     ViewModel.value.atribuidaPara = value.atribuidaPara;
              //     ViewModel.value.descricao = value.descricao;
              //     ViewModel.value.value = value.value;
              //     ViewModel.value.idBanco = key;
              //     ViewModel.value.concluida = value.concluida;
              //     ViewModel.value.deadline = value.deadline;
              //     ViewModel.value.dataCriacao = value.dataCriacao;
              //     ViewModel.value.dataUltimaAlteracao =
              //       value.dataUltimaAlteracao;
              //     ViewModel.value.dataUltimaAlteracao =
              //       value.dataUltimaAlteracao;
              //   }
              // }
            } else {
              console.log("No data available");
            }
          })
          .catch((error) => {
            console.error(error);
          });
      };
      try {
        if (tarefaId !== undefined) {
          ObterTarefa(tarefaId);
        }
      } catch (err) {
        console.error("Erro ao obter usuario para edição", err);
      }
    });
    return {
      ViewModel,
      optionsUsuarios,
      limpaForm,
      validarForm,
      mostrarBtnSalvar,
      inputReadonly,
      tarefaId,
      mensagemChat,
      chatMensage,
      optionsTipo,
      optionsPagemento,
      optionsMes,
      optionsTipoPostagem,
      voltar,
    };
  },
};
</script>
<style src="@vueform/multiselect/themes/default.css"></style>
    

